
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import LotteryService from "@/store/modules/lottery/service/lottery.service";
import ILottery from "@/store/modules/lottery/interface/lottery.interface";
import ISite from "@/store/modules/site/interface/site.interface"
import SiteService from "@/store/modules/site/service/site.service";
import * as validationHelper from "@/store/helpers/validation.helper";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect';
import XLSX from 'xlsx';
import {
  apiUrl
} from "@/core/helpers/config";

export default defineComponent({
	name: "LotteriesCreate",
	components: {
		Multiselect
	},
	setup() {
		const router = useRouter();

		const lotteryService = new LotteryService();
		const siteService = new SiteService();
		const sites = ref<Array<ISite>>([]);
		const lottery = ref<ILottery>({
			id: null,
			name: '',
			visible: false,
			duration: 0,
			position: '',
			completed: false,
			winners: [],
			site_id: null
		});

		const getSites = () => siteService.getData()
		.then((response) => {
			sites.value = [...[{id: null, name: 'Todas las sedes'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const validateForm = () => {
			let disabled = false;
			if(lottery.value.name.length == 0) disabled = true;
			return disabled
		}

		const removeWinner = (index: any) => {
			lottery.value.winners?.splice(index, 1);
		}

		const changeFile = (e) => {
			if(e.target.files.length > 0){
				let fileReader = new FileReader();
				
				fileReader.onload = (event: any) => {
					let data = event.target.result
					let sheetBook = XLSX.read(data, {
                        type: 'binary',
                        cellDates: true
                    });
					sheetBook.SheetNames.forEach(sheet => {
						let row = XLSX.utils.sheet_to_json(sheetBook.Sheets[sheet])
						let jsonObject = JSON.stringify(row)
						row.forEach((col: any) => {
							let register = {
								name: '',
								last_name: '',
								document: '',
								options: ''
							}
							for(let columnName in col){
                                if(columnName.trim().toLowerCase() == "nombres"){
                                    register.name = col[columnName]
                                }
                                else if(columnName.trim().toLowerCase() == "apellidos"){
                                    register.last_name = col[columnName]
                                }
								else if(columnName.trim().toLowerCase() == "dni"){
                                    register.document = col[columnName]
                                }
								else if(columnName.trim().toLowerCase() == "opciones"){
                                    register.options = col[columnName]
                                }
                            }

                            lottery.value.winners?.push(register)
						})
					});
				}

				fileReader.readAsBinaryString(e.target.files[0])
			}
		}

		const saveData = () => {
			Swal.fire({
				title: 'Confirmacin!',
				text: "Esta seguro que desea guardar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						lotteryService.create(lottery.value)
						.then((response) => {
							Swal.fire(
							'Confirmacin!',
							'Informacin guardada exitosamente',
							'success'
							).then(result => {
								router.push({ name: "lotteries" });
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los datos',
							'error'
							);
						});
					}
			})
			
		}

		onMounted(() => {
			getSites()
		});

		return {
			changeFile,
			saveData,
			validateForm,
			removeWinner,
			lottery,
			apiUrl,
			sites,
			validationHelper
		}
	},
});
